.disciplines {
  background-color: $background-dark;
  padding: 20vh 0;
  color: $color-secondary-light;
  &__left-content {
    position: relative;
    h1 {
      color: #fff;
      margin: 0 0 6.4rem 0;

      &::after{
        content: '';
        display: block;
        height: 3px;
        background: $red;
        width: 58%;
        position: absolute;
        margin: 72px 0 0 0;
      }
    }
    &_pencil {
      position: absolute;
      right: 0;
      margin: -20px 54px 0 0;
    }
  }
}