.production-list {
	margin: 30px 0;
	&__title {
		width: 400px;
		font: {
			family: $font-heading-heavy;
			size: 48px;
		}
		margin-top: 35px;
	}
	&__description {
		line-height: 22px;
		font: {
			family: $font-heading-regular;
			size: 18px;
		}
		margin: 20px 0;
	}
	&__link {
		font: {
			family: $font-heading-bold;
			size: 16px;
		}
		&-img {
			margin-left: 5px;
		}
	}
	&__miniature {
		&-follower {
			margin-left: -60px;
		}
		&-macbook {
			margin-left: -50px;
			margin-top: -30px;
		}
		&-bomb {
			margin-left: -196px;
			margin-top: -26px;
		}
		&-hand {
			margin-left: -55px;
			margin-top: -120px;
		}
	}
}