/*Typography*/
$font-heading-heavy: 'SFUIDisplay-Heavy';
$font-heading-semibold: 'SFUIDisplay-Semibold';
$font-heading-bold: 'SFUIDisplay-Bold';
$font-heading-regular: 'SFUIDisplay-Regular';
$font-paragraph-primary: 'Roboto';

$color-primary: #01091E;
$color-secondary: #1A1A1A;
$color-secondary-light: #FFF;

html {
  font-size: 10px !important;
}

$text-lg: 1.8rem;
$text-md: 1.6rem;
$text-sm: 1.4rem;
$text-xs: 1.2rem;

$heading-lg: 6.4rem;
$heading-md: 4.8rem;
$heading-sm: 3.6rem;
$heading-xs: 2.4rem;
$heading-xs-caps: 1.8rem;

/*Palette*/
$background-dark-secondary: #0428AE;
$background-light: #F4F6FF;
$background-dark: #0039CB;

$yellow: #FFEB3B;
$black: #292929;
$red: #F50057;

