h1, h2{
  font-family: $font-heading-heavy;
}
h3, h4 {
  font-family: $font-heading-bold;
}
h1 {
  font-size: $heading-lg;
  line-height: 7.2rem;
  color: $color-primary;
  }
h2 {
  font-size: $heading-md;
  line-height: 5.4rem;
}
h3 {
  font-size: $heading-sm;
  line-height: 3.8rem;
}

h4 {
  font-size: $heading-xs;
}

h5 {
  font-family: $font-paragraph-primary;
  font-size: $heading-xs-caps;
  text-transform: uppercase;
  font-weight: 400;
}
p {
  font-family: $font-paragraph-primary;
  font-size: $text-lg;
  line-height: 150%;
  min-width: 240px;
  max-width: 480px;
}