.slider-projects {
	background-color: $background-dark;
	@include angled-edge('outside top', 'upper right', $background-dark, 140);
	@include angled-edge('outside bottom', 'lower left', $background-dark, 140);
	padding: 20px 0 120px;
	margin: 160px 0;
	&__title {
		font: {
			family: $font-heading-heavy;
			size: 64px;
		}
		color: #fff;
		margin-bottom: 30px;
	}
	&__description {
		line-height: 20px;
		font: {
			family: $font-heading-regular;
			size: 18px;
		}
		color: #fff;
		margin-bottom: 20px;
		margin-top: 120px;
	}
	&__link {
		font: {
			family: $font-heading-heavy;
			size: 18px;
		}
		color: #fff;
		img {
			margin-left: 5px;
		}
	}

	&__item {
		min-width: 580px;
		overflow: hidden;
		padding-bottom: 10px;
		&-content {
			width: 280px;
		}
	}

	#scrolling { /* Carousel Frame */
		overflow: hidden;
	}

	.slider-projects__scrolling {
		height: 350px;

		ul {
			transform: translate3d(0px, 0px, 0px);
			width: auto !important;
			height: 350px;
			margin: 0;
			padding: 0;
			list-style-type: none;
			overflow: hidden;
			position: absolute;
			transform-style: preserve-3d;
			display: flex;
			flex-wrap: nowrap;
		}
	}
}