.features {
  margin: 50vh 0 0 0;
  overflow: hidden;
  &__item-content {
    padding: 50% 0 0 0;
    &_zig-zag {
      margin: 10vh 0 0 15vw;
      right: 0;
      bottom: 0;
    }
  }
  .item-workspace_first {
    margin: 0 0 0 10vw;
  }
  .item-workspace_last {
    margin: 0 0 0 -10vw;
  }
  &__item-workspace {
    position: relative;

    &_triang {
      position: absolute;
    }
    &_book {
      position: absolute;
      top: 30vh;
      margin: 0 0 0 5vw;
    }
    &_squad {
      position: absolute;
      padding: 12vh 0 0 0;
    }
    &_pages {
      position: absolute;
      left: 0;
      margin: 24vh 0 0 -8vh;
      width: 100%;
    }

  }
}