.production {
	padding: 140px 0 120px;
	&-fluid {
		position: relative;
		.container {
			position: absolute;
			right: 0;
			left: 0;
			top: 0;
		}
	}
	&-parallax-scene .parallax-elem {
		&-1 {
			width: 170px;
			z-index: 1;
			position: absolute;
			top: 200px !important;
		}
		&-2 {
			width: 170px;
			position: absolute;
			top: 120px !important;
		}
		&-3 {
			width: 300px;
			z-index: 1;
			right: 80px !important;
			left: unset !important;
		}
		&-4 {
			width: 160px;
			right: 25px !important;
			left: unset !important;
		}
		&-5 {
			width: 160px;
			left: 350px !important;
			top: 20px !important;
		}
		&-6 {
			width: 160px;
			left: 160px !important;
			bottom: 0 !important;
			top: unset !important;
		}
		&-7 {
			width: 160px;
			bottom: 0 !important;
			right: 25px !important;
			left: unset !important;
			top: unset !important;
		}
	}
	&__title {
		max-width: 400px;
		text-align: center;
		font: {
			family: $font-heading-heavy;
			size: 64px;
		}
		margin: 0 auto 40px;
		&:before {
			content: '';
			border-radius: 50%;
			background: linear-gradient(to bottom, rgba(255,235,59,1) 0%, rgba(145,232,66,0) 100%);
			width: 365px;
			height: 365px;
			display: block;
			margin: 0 auto -290px;
		}
	}
	&__description {
		max-width: 470px;
		text-align: center;
		line-height: 22px;
		font: {
			family: $font-heading-regular;
			size: 18px;
		}
		margin: 0 auto;
	}
}