.header {
  height: 100vh;
  .content {
    transform: rotate(-45deg);
    margin: 5vw -3.9vw 0 0;
    position: absolute;
    right: 20vw;
    z-index: 1;
  }
  .overlay {
    &__figure {
      position: absolute;
      &_rect,
      &_triang {
        top: 0;
      }
      &_rect {
        left: 0;
      }
      &_triang {
        left: 25%;
      }
      &_line_red {
        left: 11vw;
      }
      &_line_purp {
        right: 0;
        top: 39vh;
      }
      &_line_yellow {
        right: 0;
      }
      &_cup {
        right: 0;
        top: 80vh;
      }
    }
  }

}